.questions {
    //    display: none;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    overflow-x: hidden;
    overflow-y: hidden;
    transform: translateX(100%);
    @media only screen and (max-height: 800px) {
        overflow-y: scroll;
    }
}

.question {
    position: absolute;
    top: 0;
    left: 0;
    margin: 0;
    width: 100%;
    transform: translateX(100%);
    -ms-transform: translateX(100%);
    z-index: 1;
    &:first-of-type {
        display: block;
        transform: translateX(0%);
        -ms-transform: translateX(0%);
    }
    @media #{$iphone5} {
        min-height: 900px !important;
    }
    .question_content {
        position: absolute;
        width: 100%;
        top: 50%;
        transform: translateY(-50%);
        @media #{$allPhones} {
            position: static !important;
            transform: translateY(0) !important;
            top: 0 !important;
        }
    }
    .text_wrapper {
        width: 90%;
        max-width: 800px;
        margin: 0 auto;
        text-align: center;
        color: #ffffff;
        margin-bottom: 2.5rem;
        margin-top: 1.5rem;
    }
    .slider_wrapper {
        width: 80%;
        margin: 0 auto;
        max-width: 800px;
        .slider {
            width: 82%;
            margin: 0 auto;
            margin-bottom: 2.5rem;
            @media #{$allPhones} {
                margin-bottom: 4rem;
            }
        }
        .noUi-handle {
            transition: all .2s cubic-bezier(0.61, 0.35, 0.42, 2);
            &:hover {
                transform: scale(1.2);
            }
            @media #{$allPhones} {
                left: -38px;
                &:hover {
                    transform: scale(1.1);
                }
            }
        }
        .slider_labels {
            width: 100%;
            text-align: justify;
            margin-bottom: 2.5rem;
            .money_stack {
                display: inline-block;
                width: 10%;
                margin-right: ((100%-(10*5))/5);
                transform: translateX(46%);
                transition: all .2s cubic-bezier(0.61, 0.35, 0.42, 2);
                cursor: pointer;
                &:hover {
                    transform: scale(1.2) translateX(43%);
                }
                &:last-of-type {
                    margin-right: 0;
                }
                img {
                    max-width: 100%;
                }
                @media #{$allPhones} {
                    width: 18%;
                    left: -20px;
                    position: relative;
                    &:last-of-type {
                        margin-right: 0;
                    }
                }
                @media #{$iphone6} {
                    margin-right: 0.85%;
                }
                @media #{$iphone5} {
                    margin-right: 0.65%;
                }
                @media #{$allTablets} {
                    width: 15%;
                    margin-right: 4%;
                    left: -20px;
                    position: relative;
                }
            }
            @media #{$allPhones} {
                margin-bottom: 3rem;
            }
        }
        .slider_instructions {
            p {
                text-align: center;
                color: #ffffff;
            }
            margin-bottom: 1.5rem;
        }
        @media #{$allPhones} {
            width: 90%;
        }
    }
    button {
        display: block;
        position: relative;
        z-index: 2;
        width: 90%;
        max-width: 800px;
        margin: 0 auto;
        color: $greenText;
        background-color: #ffffff;
        padding: 10px 0;
        font-size: 1rem;
        border: 0;
        outline: 0;
        border-bottom: 5px solid rgba(0, 0, 0, 0.2);
        cursor: pointer;
        transition: all ease-out .33s;
        margin-bottom: 1.5rem;
        text-transform: uppercase;
        &:hover,
        &:focus {
            background-color: darken(#ffffff, 5%);
        }
    }
    #mktoForm_1256 {
        width: 100% !important;
    }
    .mktoButtonRow {
        width: 100% !important;
        display: block !important;
        button {
            display: block !important;
            position: relative !important;
            color: $greenText !important;
            font-family: 'Roboto Condensed', sans-serif !important;
            font-weight: 400 !important;
            text-transform: uppercase !important;
            z-index: 2 !important;
            width: 90% !important;
            max-width: 800px !important;
            margin: 0 auto !important;
            background-color: #ffffff !important;
            padding: 10px 0 !important;
            font-size: 1rem !important;
            border: 0 !important;
            outline: 0 !important;
            border-bottom: 5px solid rgba(0, 0, 0, 0.2) !important;
            cursor: pointer !important;
            transition: all ease-out .33s !important;
            margin-bottom: 1.5rem !important;
            border-radius: 0px !important;
            &:hover,
            &:focus {
                background-color: darken(#ffffff, 5%) !important;
            }
        }
    }
    button#q1Next {
        opacity: 0;
        visibility: hidden;
    }
    button.back_button {
        background-color: rgba(255, 255, 255, 0);
        color: #ffffff;
        font-size: 22px;
        line-height: 25px;
        max-width: 250px;
        padding: 0px;
        transition: all ease-out .25s;
        &:hover,
        &:focus {
            transform: translateX(-5px);
        }
        &:active {
            transform: translateX(-10px);
        }
        img {
            max-width: 20px;
            height: 20px;
            margin-right: 10px;
            display: inline-block;
            vertical-align: middle;
        }
    }
    background-size: cover;
    background-position: 0 0;
    &:after {
        content: '';
        display: block;
        width: 100%;
        height: 43%;
        position: absolute;
        bottom: 0;
        left: 0;
        background-color: rgba(0, 0, 0, 0.8);
        z-index: -1;
        @media #{$allPhones} {
            height: 50%;
        }
    }
    
}

